import Vue from 'vue';

export const getAsyncMutationTypes = actionType => ({
  setError: `${actionType}SetError`,
  setData: `${actionType}SetData`,
  setLoading: `${actionType}SetLoading`,
});

export const createAsyncAction = ({ actionType, asyncFn, errorHandlers }) => {
  const result = {};
  const { setError, setData, setLoading } = getAsyncMutationTypes(actionType);
  result[actionType] = async ({ commit }, payload) => {
    commit(setLoading, true);

    try {
      const response = await asyncFn(payload);
      commit(setLoading, false);
      commit(setError, null);
      commit(setData, response);
      return response;
    } catch (error) {
      if (error.response && errorHandlers[error.response.status]) {
        errorHandlers[error.response.status](error);
      }
      commit(setLoading, false);
      commit(setData, null);
      commit(setError, error);
      throw error;
    }
  };

  return result;
};

export const createAsyncMutations = (actionType) => {
  const { setError, setData, setLoading } = getAsyncMutationTypes(actionType);
  return {
    [setLoading]: (state, value) => {
      Vue.set(state, actionType, {
        ...state[actionType],
        loading: value,
      });
    },
    [setData]: (state, response) => {
      Vue.set(state, actionType, {
        ...state[actionType],
        data: response,
      });
    },
    [setError]: (state, error) => {
      Vue.set(state, actionType, {
        ...state[actionType],
        error: error ? { ...error, str: undefined } : null,
      });
    },
  };
};

/**
 * Creates a store object that can be used as a Vuex module
 *
 * @param [{actionType: string, asyncFn: function,
 *  errorHandlers: { errorCodeKey: function } }] actionsData
 * @return { state, getters, actions, mutations }
 */
export const createAsyncStore = (actionsData) => {
  const { actions, mutations } = actionsData.reduce(
    // The accumulation function
    (acc, action) => {
      const { actions: accActions, mutations: accMutations } = acc;
      const { actionType, asyncFn, errorHandlers } = action;

      return {
        actions: {
          ...accActions,
          ...createAsyncAction({ actionType, asyncFn, errorHandlers }),
        },
        mutations: {
          ...accMutations,
          ...createAsyncMutations(actionType),
        },
      };
    },
    // The initial accumuation state
    {
      actions: {},
      mutations: {},
    },
  );

  const state = actionsData.reduce((acc, { actionType }) => {
    acc[actionType] = {
      data: null,
      error: null,
      loading: false,
    };
    return acc;
  }, {});

  return {
    state,
    actions,
    mutations,
  };
};
